<template>
    <div>
        <h1>{{id ? '编辑':'新建'}}英雄</h1>
        <el-form label-width="80px" @submit.native.prevent="save">
            <!-- <el-form-item label="上级分类">
                <el-select v-model="model.parent">
                    <el-option v-for="item in parents" :key="item._id" :label="item.name" :value="item._id"></el-option>
                </el-select>
            </el-form-item> -->
            <el-tabs type="border-card">
                <el-tab-pane label="基本信息">
                    <el-form-item label="名称">
                        <el-input v-model="model.name"></el-input>
                    </el-form-item>
            <el-form-item label="头像">
                <el-upload
                    class="avatar-uploader"
                    :action="uploadUrl"
                    :headers="getAuthHeaders()"
                    :show-file-list="false"
                    :on-success="res=> $set(model,'avatar',res.url)"
                    >
                    <img v-if="model.avatar" :src="model.avatar" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="Banner">
                <el-upload
                    class="avatar-uploader"
                    :action="uploadUrl"
                    :headers="getAuthHeaders()"
                    :show-file-list="false"
                    :on-success="res=> $set(model,'banner',res.url)"
                    >
                    <img v-if="model.banner" :src="model.banner" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="称号">
                <el-input v-model="model.title"></el-input>
            </el-form-item>
            <el-form-item label="类型">
                    <el-select v-model="model.categories" multiple>
                        <el-option v-for="item of categories" :key="item._id" :label="item.name" :value="item._id"></el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label="难度">
                <el-rate style="margin-top:0.6rem" :max='9' show-score v-model="model.scores.difficult"></el-rate>
            </el-form-item>
            <el-form-item label="技能">
                <el-rate style="margin-top:0.6rem" :max='9' show-score v-model="model.scores.skills"></el-rate>
            </el-form-item>
            <el-form-item label="攻击">
                <el-rate style="margin-top:0.6rem" :max='9' show-score v-model="model.scores.attack"></el-rate>
            </el-form-item>
            <el-form-item label="生存">
                <el-rate style="margin-top:0.6rem" :max='9' show-score v-model="model.scores.survive"></el-rate>
            </el-form-item>
            <el-form-item label="顺风出装">
                <el-select v-model="model.items1" multiple>
                        <el-option v-for="item of items1" :key="item._id" :label="item.name" :value="item._id"></el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label="逆风出装">
                <el-select v-model="model.items2" multiple>
                        <el-option v-for="item of items2" :key="item._id" :label="item.name" :value="item._id"></el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label="使用技巧">
                <el-input type="textarea" v-model="model.usageTips"></el-input>
            </el-form-item>
            <el-form-item label="对抗技巧">
                <el-input type="textarea" v-model="model.battleTips"></el-input>
            </el-form-item>
            <el-form-item label="团战思路">
                <el-input type="textarea" v-model="model.teamTips"></el-input>
            </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="技能" name="skills">
                    <el-button type="primary" size="small" @click="model.skills.push({})">添加技能</el-button>
                    <el-row type="flex" style="flex-wrap:wrap">
                        <el-col :md="12" v-for="(item,i) in model.skills" :key="i">
                            <el-form-item label="名称">
                                <el-input v-model="item.name"></el-input>
                            </el-form-item>
                                <el-form-item label="图标">
                                    <el-upload
                                        class="avatar-uploader"
                                        :action="uploadUrl"
                                        :headers="getAuthHeaders()"
                                        :show-file-list="false"
                                        :on-success="res=> $set(item,'icon',res.url)"
                                        >
                                        <img v-if="item.icon" :src="item.icon" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                        </el-upload>
                                </el-form-item>
                            <el-form-item label="冷却值">
                                <el-input  v-model="item.delay"></el-input>
                            </el-form-item>
                            <el-form-item label="消耗">
                                <el-input v-model="item.cost"></el-input>
                            </el-form-item>
                            <el-form-item label="描述">
                                <el-input type="textarea" v-model="item.description"></el-input>
                            </el-form-item>
                            <el-form-item label="小提示">
                                <el-input type="textarea" v-model="item.tips"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button size="small" type="danger" @click="model.skills.splice(i,1)">删除</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>

                <el-tab-pane label="最佳搭档" name="partners">
                    <el-button type="primary" size="small" @click="model.partners.push({})">添加英雄</el-button>
                    <el-row type="flex" style="flex-wrap:wrap">
                        <el-col :md="12" v-for="(item,i) in model.partners" :key="i">
                            <el-form-item label="英雄">
                                <el-select filterable v-model="item.hero">
                                    <el-option v-for="hero in heros" :key="hero._id" :value="hero._id" :label="hero.name"></el-option>
                                </el-select>
                            </el-form-item>                 
                             <el-form-item label="描述">
                                <el-input type="textarea" v-model="item.description"></el-input>
                            </el-form-item>
  
                            <el-form-item>
                                <el-button size="small" type="danger" @click="model.partners.splice(i,1)">删除</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
            <el-form-item style="margin-top:1rem" lable="按钮">
                <el-button type="primary" native-type="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

export default {
    name:'CategoryEditVue',
    props:{
        id:{}
    },
    data(){
        return{
            heros:[],
            categories:[],
            items1:[],
            items2:[],
            model:{
                name:'',
                avatar:'', 
                title:'',
                partners:[],
                scores:{
                    difficult: 0
                },
                skills:[]
            },
        }
    },
    methods: {
        async save(){
            let res;
            if (this.id) {
                const res = await this.$http.put(`rest/heros/${this.id}`,this.model)
               
            }else{
                 const res = await this.$http.post('rest/heros',this.model)
            }
            this.$router.push('/heros/list')
            this.$message({
                type:'success',
                message:'保存成功'
            })
        },
        async fetch(){
            const res = await this.$http.get(`rest/heros/${this.id}`)
            this.model = Object.assign({},this.model,res.data)
        },
        async fetchCategories(){
            const res = await this.$http.get(`rest/categories`)
            this.categories = res.data
        },
        async fetchItems(){
            const res = await this.$http.get(`rest/items`)
            this.items1 = res.data
            this.items2 = res.data
        },
        async fetchHeros(){
            const res = await this.$http.get(`rest/heros`)
            this.heros = res.data
        },
        
    },
    created() {         
        this.id &&  this.fetch() 
        this.fetchCategories()  
        this.fetchItems()    
        this.fetchHeros()    
    },

}
</script>

