<template>
    <div>
        <h1>{{id ? '编辑':'新建'}}视频</h1>
        <el-form label-width="80px" @submit.native.prevent="save">
            <el-form-item label="标题">
                <el-input v-model="model.name"></el-input>
            </el-form-item>
            <el-button type="primary" size="small" @click="model.items.push({})">添加视频</el-button>
            <el-row type="flex" style="flex-wrap:wrap">
                        <el-col :md="12" v-for="(item,i) in model.items" :key="i">
                            <el-form-item label="跳转链接">
                                <el-input v-model="item.url"></el-input>
                            </el-form-item>
                                <el-form-item label="图标">
                                    <el-upload
                                        class="avatar-uploader"
                                        :headers="getAuthHeaders()"
                                        :action="$http.defaults.baseURL + '/upload'"
                                        :show-file-list="false"
                                        :on-success="res=> $set(item,'image',res.url)"
                                        >
                                        <img v-if="item.image" :src="item.image" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                        </el-upload>
                                </el-form-item>
                            <el-form-item>
                                <el-button size="small" type="danger" @click="model.items.splice(i,1)">删除</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
            <el-form-item lable="按钮">
                <el-button style="margin-top:1rem" type="primary" native-type="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

export default {
    name:'CategoryEditVue',
    props:{
        id:{}
    },
    data(){
        return{
            model:{
                items:[]
            },
            parents:[],
        }
    },
    methods: {
        async save(){
            let res
            if (this.id) {
                const res = await this.$http.put(`rest/ads/${this.id}`,this.model)
               
            }else{
                 const res = await this.$http.post('rest/ads',this.model)
            }
            this.$router.push('/ads/list')
            this.$message({
                type:'success',
                message:'保存成功'
            })
        },
        async fetch(){
            const res = await this.$http.get(`rest/ads//${this.id}`)
            this.model = Object.assign({},this.model,res.data)
        }
    },
    created() {         
        this.id &&  this.fetch()        
    },

}
</script>

